import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhQuery, VhQuerySales } from 'ionic-vhframeworks';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-detail',
  templateUrl: './detail.component.html',
  styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {
  list_data: any = [];
  root: any;
  isVisibleModalAdd: boolean = false;
  coupons: any = [];
  list_show_coupons: any = [];
  constructor(
    private router: Router,
    private cdRef: ChangeDetectorRef,
    public vhAlgorithm: VhAlgorithm,
    private vhQuerySales: VhQuerySales,
    private vhComponent: VhComponent,
    private languageService: LanguageService,
    private vhQuery:VhQuery
  ) {
    this.root = this.router.getCurrentNavigation().extras.state
  }

  ngOnInit(): void {
    this.getData()
  }

  /**
   * get d liu CouponRelaseDetail v coupons  chn vo t
   */
  getData() {
    Promise.all([
      this.vhQuerySales.getCouponRelaseDetail_byIdCouponRelease( this.root.id_coupon_release ),
      this.vhQuerySales.getCoupons(),
    ])
      .then(([release_details, coupons]: any) => {
        this.coupons =  coupons
        this.list_data = release_details.map(item => { return { ...item, coupon_name : this.coupons.find(ele => ele._id == item.id_coupon).name , value : this.coupons.find(ele => ele._id == item.id_coupon).units[0].value, id_design_barcode : this.coupons.find(ele => ele._id == item.id_coupon).id_design_barcode }})
      })
  }

  /**
   * m modal chn coupn vo t pht hnh
   */
  addCoupon() {
    this.isVisibleModalAdd = true;
    this.list_show_coupons = this.coupons.filter(ele => !this.list_data.find(i => i.id_coupon == ele._id)).map(item => { return { label: item.name, name: item.name, value: item._id, value_coupon: item.units[0].value } })
    

  }

  heightScroll: string;
  ngAfterViewChecked() {
    if (document.querySelector("#product-list") && document.querySelector(".ant-table-thead") && document.querySelector(".product-list-header")) {
      this.heightScroll = document.querySelector("#product-list").clientHeight - document.querySelector(".ant-table-thead").clientHeight
        - document.querySelector(".product-list-header").clientHeight - 50 + "px";
    }
    this.cdRef.detectChanges();
  }

  goBack() {
    this.router.navigate(['/sales/dashboard/manage/sales-program/coupons/publish-coupon'])
  }

  gotoReleased(coupon) {
    this.router.navigate(['/sales/dashboard/manage/sales-program/coupons/publish-coupon/detail/released'], { state: { ...coupon, name: this.root.name, date_validity : this.root.date_validity, date_expire : this.root.date_expire } })
  }

  /**
   * ng modal chn coupon vo t v ko lu
   */
  handleCancel() {
    this.isVisibleModalAdd = false
  }

  /**
   * x l thm coupon vo t pht hnh
   */
  handleOk() {
    let arr = this.list_show_coupons.filter(item => item.checked)
    this.isVisibleModalAdd = false;
    let promise = new Array();
    for (let i in arr) {
      promise[i] = this.vhQuerySales.addCouponReleaseDetail(  {
        id_coupon: arr[i].value,
        id_coupon_release: this.root.id_coupon_release,
        quantity_released : 0
      })
    }
    this.vhQuery.asyncPromiseAll(promise).then((array) => {
      this.list_data = this.list_data.concat(arr.map((item, index) => {
        return {
          name: item.name, _id: array[index]._id, quantity_released: 0, value: item.value_coupon, coupon_name: item.name, id_coupon: array[index].id_coupon,
          id_coupon_release: this.root.id_coupon_release, id_design_barcode : item.id_design_barcode
        }
      }))
      

    },
      error => { })

  }

  /**
   * xa coupon khi t ny
   */
	deleteCouponsOfRelease(data) {
		this.vhComponent.showLoading("", "transparent-loading").then(() => {
			this.vhQuerySales.deleteCouponReleaseDetail(data._id)
				.then((rsp:any)=>{
					this.vhComponent.hideLoading(0);
					if(rsp.vcode == 0){
						this.list_data = this.list_data.filter(item => item._id != data._id)
						this.vhComponent.alertMessageDesktop("success", this.languageService.translate("Xa thnh cng"))
					}else if(rsp.vcode == 1){
						this.vhComponent.alertMessageDesktop("error", this.languageService.translate("Khng th xa v c phiu thanh ton lin quan  c i im hoc s dng thanh ton"),8000)
					}
				}, error=>{
					this.vhComponent.hideLoading(0);
					
				})
		})
	}

  /** hm sort cho cc ct */
  coupon_nameCol = false;
  valueCol = false;
  quantity_releasedCol = false;
  /** Hm thc hin sp xp theo collection
   *
   * @param colName       // tn ct mun sp xp
   */
  sortTable(colName){
    switch (colName) {
      case 'coupon_name':
        if(this.coupon_nameCol){
          this.list_data = this.vhAlgorithm.sortVietnamesebyASC([...this.list_data], colName);
        }else{
          this.list_data = this.vhAlgorithm.sortVietnamesebyDESC([...this.list_data], colName);
        }
        break;
      case 'value':
        if(this.valueCol){
          this.list_data = this.vhAlgorithm.sortNumberbyASC([...this.list_data], colName);
        }else{
          this.list_data = this.vhAlgorithm.sortNumberbyDESC([...this.list_data], colName);
        }
        break;
      case 'quantity_released':
        if(this.quantity_releasedCol){
          this.list_data = this.vhAlgorithm.sortNumberbyASC([...this.list_data], colName);
        }else{
          this.list_data = this.vhAlgorithm.sortNumberbyDESC([...this.list_data], colName);
        }
        break;
    }
  }
}
