import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'app-coupons',
  templateUrl: './coupons.component.html',
  styleUrls: ['./coupons.component.scss']
})
export class CouponsComponent implements OnInit {

  constructor(
    private vhComponent :VhComponent,
    private router:Router
  ) { }

  ngOnInit(): void {
  }

  gotoCoupons(){
    this.router.navigate(['/sales/dashboard/manage/sales-program/coupons/list-coupons'])
  }

  gotoPublish(){
    this.router.navigate(['/sales/dashboard/manage/sales-program/coupons/publish-coupon'])
  }

  goBack() {
    this.router.navigate(['/sales/dashboard/manage/sales-program'])
  }

  gotoDesign(){
    this.router.navigate(['/sales/dashboard/manage/sales-program/coupons/design'])

  }
  gotoExchangePoint(){
    this.router.navigate(['/sales/dashboard/manage/sales-program/coupons/exchange-points'])

  }
}
