import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { CouponsComponent } from './coupons.component';
import { RouterModule, Routes } from '@angular/router';
import { IonicModule } from '@ionic/angular';
import { TranslateModule } from '@ngx-translate/core';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { ListCouponsComponent } from './list-coupons/list-coupons.component';
import { PublishCouponsComponent } from './publish-coupons/publish-coupons.component';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { DetailComponent } from './publish-coupons/detail/detail.component';
import { ReleasedComponent } from './publish-coupons/detail/released/released.component';
import { PrintComponent } from './publish-coupons/detail/released/print/print.component';

const routes: Routes = [
  {
    path: '',
    component: CouponsComponent
  },
  {
    path: 'list-coupons',
    component: ListCouponsComponent
  },
  {
    path: 'publish-coupon',
    component: PublishCouponsComponent
  },
  {
    path: 'publish-coupon/detail',
    component: DetailComponent
  },
  {
    path: 'publish-coupon/detail/released',
    component: ReleasedComponent
  },
  {
    path: 'design',
    loadChildren: () => import('./design/design.module').then(m => m.DesignModule)
  },
  {
    path: 'exchange-points',
    loadChildren: () => import('./exchange-points/exchange-points.module').then(m => m.ExchangePointsModule)
  },
]

@NgModule({
  declarations: [
    CouponsComponent,
    ListCouponsComponent,
    PublishCouponsComponent,
    DetailComponent,
    ReleasedComponent,
    PrintComponent,

  ],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    IonicModule,
    TranslateModule,
    FormsModule,
    ReactiveFormsModule
  ]
})
export class CouponsModule { }
