import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { VhAlgorithm, VhQuery, VhQuerySales } from 'ionic-vhframeworks';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-publish-coupons',
  templateUrl: './publish-coupons.component.html',
  styleUrls: ['./publish-coupons.component.scss']
})
export class PublishCouponsComponent implements OnInit {
  isVisibleModalAdd: boolean = false
  isVisibleModalEdit: boolean = false
  list_data: any = []
  data_selected: any = {};
  form: FormGroup
  form_edit: FormGroup
  constructor(
    private router: Router,
    private cdRef: ChangeDetectorRef,
    public vhAlgorithm: VhAlgorithm,
    private vhQuerySales: VhQuerySales,
    private vhComponent: VhComponent,
    private languageService: LanguageService
  ) { }

  ngOnInit(): void {
    this.vhQuerySales.getCouponReleases()
      .then((response: any) => {
        this.list_data = response
        
      })

  }
  // khi to form thm t pht hnh coupon
  initForm() {
    this.isVisibleModalAdd = true;
    this.form = new FormGroup({
      name: new FormControl('', Validators.required),
      date_validity: new FormControl(new Date(new Date().setHours(0, 0, 0)), Validators.required),
      date_expire: new FormControl(new Date(new Date().setHours(23, 59, 59)), Validators.required)
    })
  }

  goBack() {
    this.router.navigate(['/sales/dashboard/manage/sales-program/coupons'])
  }
  heightScroll: string;
  ngAfterViewChecked() {
    if (document.querySelector("#product-list") && document.querySelector(".ant-table-thead") && document.querySelector(".product-list-header")) {
      this.heightScroll = document.querySelector("#product-list").clientHeight - document.querySelector(".ant-table-thead").clientHeight
        - document.querySelector(".product-list-header").clientHeight - 50 + "px";
    }
    this.cdRef.detectChanges();
  }
 //  Thm t pht hnh 
  addRelease() {
    if (new Date(this.form.value.date_validity) < new Date(this.form.value.date_expire))
      this.vhComponent.showLoading('').then(() => {
        this.vhQuerySales.addCouponRelease(this.form.value)
          .then((response) => {
            this.vhComponent.hideLoading(0)
            this.isVisibleModalAdd = false;
            this.list_data.push(response)
            this.vhComponent.alertMessageDesktop("success", this.languageService.translate("Thm t pht hnh thnh cng"))
          }, error => {
            
            this.vhComponent.alertMessageDesktop("error", this.languageService.translate("Thm t pht hnh tht bi"))
          })
      })

    else {
      this.vhComponent.alertMessageDesktop("error", this.languageService.translate("Staring date must be less than or the same as ending date"))
    }
  }
  /**
   * gn gi tr ca t pht hnh vo form  cp nht
   */
  initFormEdit(data) {
    this.isVisibleModalEdit = true;
    this.form_edit = new FormGroup({
      _id: new FormControl(data._id, Validators.required),
      name: new FormControl(data.name, Validators.required),
      date_validity: new FormControl(new Date(data.date_validity), Validators.required),
      date_expire: new FormControl(new Date(data.date_expire), Validators.required)
    })
  }

  /**
   * cp nht t pht hnh ln DB
   */
  save() {
    this.vhQuerySales.updateCouponRelease(this.form_edit.value._id, this.form_edit.value)
      .then((bool) => {
        this.isVisibleModalEdit = false
        this.list_data.find(item => item._id == this.form_edit.value._id).name = this.form_edit.value.name
        this.list_data.find(item => item._id == this.form_edit.value._id).date_validity = this.form_edit.value.date_validity
        this.list_data.find(item => item._id == this.form_edit.value._id).date_expire = this.form_edit.value.date_expire
      }, error => {
        
        this.vhComponent.alertMessageDesktop("error", this.languageService.translate("Cp nht t pht hnh tht bi"))
      })
  }

  /**
   * xa t pht hnh khi DB
   */
  deleleData(data, index) {
    this.vhComponent.showLoading("", "transparent-loading").then(() => {
      this.vhQuerySales.deleteCouponRelease(data._id)
        .then((rsp: any) => {
          this.vhComponent.hideLoading(0);
          if (rsp.vcode == 0) {
            this.vhComponent.alertMessageDesktop("success", this.languageService.translate("Xa t pht hnh thnh cng"))
            this.list_data.splice(index, 1)
          } else if (rsp.vcode == 1) {
            this.vhComponent.alertMessageDesktop("error", this.languageService.translate("Xa khng thnh cng! Vui lng xa cc phiu  chn trong t pht hnh ny trc!"), 8000)
          }
        }, error => {
          this.vhComponent.hideLoading(0);
          
        })
    })

  }

  gotoDetail(data) {
    this.router.navigate(['/sales/dashboard/manage/sales-program/coupons/publish-coupon/detail'], { state: { ...data, id_coupon_release: data._id } })
  }


  /** hm sort cho cc ct */
  nameCol = false;
  /** Hm thc hin sp xp theo collection
   *
   * @param colName       // tn ct mun sp xp
   */
  sortTable(colName) {
    switch (colName) {
      case 'name':
        if (this.nameCol) {
          this.list_data = this.vhAlgorithm.sortVietnamesebyASC([...this.list_data], colName);
        } else {
          this.list_data = this.vhAlgorithm.sortVietnamesebyDESC([...this.list_data], colName);
        }
        break;
    }
  }

}
