import QRCode from 'qrcode';
import { ChangeDetectorRef, Component, Input, OnInit, SimpleChanges } from '@angular/core';
import { Router } from '@angular/router';
import JsBarcode from 'jsbarcode';
import printJS from 'print-js';

@Component({
  selector: 'app-print',
  templateUrl: './print.component.html',
  styleUrls: ['./print.component.scss']
})
export class PrintComponent implements OnInit {
  @Input() stamp: any;
  @Input() start_print: any;
  @Input() data: any;
  heightScroll: any;
  constructor(
    private router: Router,
    private cdf: ChangeDetectorRef
  ) {
    // this.stamp = this.router.getCurrentNavigation().extras.state;
    // 

  }

  ngOnInit(): void {
    
  }

  ngOnChanges({ start_print }: SimpleChanges): void {
    // 

    if (start_print && start_print.currentValue) {
      // this.renderBarcode();
      setTimeout(() => {
        this.printBrowser()
      }, 100);
    }
  }

  changeNummber() {
    // this.listRowBarcode = []
    // for (let i = 0; i < this.stamp.number; i++) {
    //   this.listRowBarcode.push(true);
    // }
  }

  ngAfterViewChecked(): void {
    if (document.querySelector(".barcode"))
      this.renderBarcode();
    if (document.querySelector('#scroll')) this.heightScroll = document.querySelector('#scroll').clientHeight - 32;
    this.cdf.detectChanges();
  }

  renderBarcode() {
    if (this.data)
      for (let i in this.data) {
        if (this.data[i].code)
          QRCode.toCanvas(document.getElementById(this.data[i].code), this.data[i].code,{ width: parseInt(this.stamp.property[3].height) * 3.78 }, function (error) {
            if (error) console.error(error)
          })
        if (this.data[i].code)
          JsBarcode(".barcode", this.data[i].code, {
            displayValue: false,

          });
      }
  }
  printBrowser() {
    printJS({
      printable: "stamp-barcode",
      type: "html",
      scanStyles: false,
      maxWidth: this.stamp.width,
      style:
        '@page { margin: 0 !important;padding: 0 !important;}' +
        '.stamp {margin-bottom: 3mm; width: ' + this.stamp.width + 'mm;height: ' + (this.stamp.height - 4) + 'mm;}' +
        '.center-all-content { display: flex;align-items: center;justify-content: center;}'
    });
  }


}
