import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { VhAlgorithm, VhQuery, VhQuerySales } from 'ionic-vhframeworks';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-list-coupons',
  templateUrl: './list-coupons.component.html',
  styleUrls: ['./list-coupons.component.scss']
})
export class ListCouponsComponent implements OnInit {
  show_add_coupon_modal: boolean = false
  show_edit_coupon_modal: boolean = false
  list_data: any = [];
  form_add: FormGroup
  form_edit: FormGroup
  coupon_selected: any;
  list_labels: any = [];
  value: any;
  constructor(
    private router: Router,
    private cdRef: ChangeDetectorRef,
    public vhAlgorithm: VhAlgorithm,
    private vhQuery: VhQuery,
    private vhQuerySales: VhQuerySales,
    private vhComponent: VhComponent,
    private languageService: LanguageService
  ) { }

  ngOnInit(): void {
    Promise.all([
      this.vhQuerySales.getCoupons(),
      this.vhQuery.getDocsByFields("barcode_designs", { type: { $eq: 2 } })
    ])
      .then(([coupons, barcode_designs]: any) => {
        this.list_data = coupons
        this.list_labels = barcode_designs
      })
  }

  goBack() {
    this.router.navigate(['/sales/dashboard/manage/sales-program/coupons'])
  }

  heightScroll: string;
  ngAfterViewChecked() {
    if (document.querySelector("#product-list") && document.querySelector(".ant-table-thead") && document.querySelector(".product-list-header")) {
      this.heightScroll = document.querySelector("#product-list").clientHeight - document.querySelector(".ant-table-thead").clientHeight
        - document.querySelector(".product-list-header").clientHeight - 50 + "px";
    }
    this.cdRef.detectChanges();
  }

  /**
   * khi to form vi cc trng ca coupon  to
   */
  initFormAdd() {
    this.form_add = new FormGroup({
      name: new FormControl('', Validators.required),
      value: new FormControl('', Validators.required),
      points: new FormControl('', Validators.required),
      id_design_barcode: new FormControl(''),
    })
    setTimeout(() => {
      if (document.querySelector(".value")) this.value = this.vhAlgorithm.vhnumeral(".value");
    }, 100);

  }

  /**
   * to coupon ln DB
   */
  addCoupons() {
    // x l phn units ca coupon  lu
    let data = this.form_add.value;
    data['units'] = [{
      value: parseFloat(this.value.getRawValue()),
      points: data.points,
      unit: '',
      ratio: 1,
      default: true
    }]
    delete data.value;
    delete data.points;

    if (this.form_add.valid) {
      this.vhComponent.showLoading('').then(() => {
        this.vhQuerySales.addCoupon(data).then((response) => {
          this.show_add_coupon_modal = false
          this.list_data.push(response);
          this.vhComponent.hideLoading(0)
          this.vhComponent.alertMessageDesktop("success", this.languageService.translate("Thm coupon thnh cng"))
        }, error => {
          
          this.vhComponent.alertMessageDesktop("error", this.languageService.translate("Thm coupon tht bi"))
        })
      })

    }
    else this.vhComponent.alertMessageDesktop("error", this.languageService.translate("Vui lng in y  thng tin"))
  }

  /**
   * gn cc gi tr ca coupon vo form  cp nht
   * @param data 
   */
  initFormEdit(data) {
    this.form_edit = new FormGroup({
      name: new FormControl(data.name, Validators.required),
      value: new FormControl(data.units[0].value, Validators.required),
      points: new FormControl(data.units[0].points, Validators.required),
      id_design_barcode: new FormControl(data.id_design_barcode),
    })
    this.show_edit_coupon_modal = true
    setTimeout(() => {
      if (document.querySelector(".value_edit")) this.value = this.vhAlgorithm.vhnumeral(".value_edit");
    }, 100);
  }

  /**
   * ng modal chnh sa coupon
   */
  cancelEdit() {
    this.show_edit_coupon_modal = false
  }

  /**
   * lu coupon t form edit vo DB
   */
  saveCoupons() {
    this.vhComponent.showLoading('').then(() => {
      let data_update = { ...this.form_edit.value }
      data_update['units'] = [{
        value: parseFloat(this.value.getRawValue()),
        points: data_update.points,
        unit: '',
        ratio: 1,
        default: true
      }]
      delete data_update.value;
      delete data_update.points;
      this.vhQuerySales.updateCoupon(this.coupon_selected._id, data_update)
        .then((bool) => {
          this.vhComponent.hideLoading(0)
          if (bool) {
            this.vhComponent.alertMessageDesktop("success", this.languageService.translate("Cp nht thnh cng"))
            this.show_edit_coupon_modal = false;
            this.list_data.find(item => item._id == this.coupon_selected._id).name = this.form_edit.value.name
            this.list_data.find(item => item._id == this.coupon_selected._id).units = data_update.units
          }
          else this.vhComponent.alertMessageDesktop("error", this.languageService.translate("Cp nht tht bi"))
        }, error => {
          
        })
    })

  }

  /**
   * xa coupon khi DB
   * @param data 
   * @param index 
   */
  deleleCoupons(data, index) {
    this.vhComponent.showLoading("", "transparent-loading").then(() => {
      this.vhQuerySales.deleteCoupon(data._id)
        .then((rsp: any) => {
          this.vhComponent.hideLoading(0);
          if (rsp.vcode == 0) {
            this.vhComponent.alertMessageDesktop("success", this.languageService.translate("Xa Coupon thnh cng"))
            this.list_data.splice(index, 1)
          } else if (rsp.vcode == 1) {
            this.vhComponent.alertMessageDesktop("error", this.languageService.translate("Xa khng thnh cng, vui lng xa phiu ny khi t pht hnh lin quan!"), 8000)
          }
        }, error => {
          this.vhComponent.hideLoading(0);
          
        })
    })
  }


  /** hm sort cho cc ct */
  nameCol = false;
  valueCol = false;
  pointsCol = false
  /** Hm thc hin sp xp theo collection
   *
   * @param colName       // tn ct mun sp xp
   */
  sortTable(colName) {
    switch (colName) {
      case 'name':
        if (this.nameCol) {
          this.list_data = this.vhAlgorithm.sortVietnamesebyASC([...this.list_data], colName);
        } else {
          this.list_data = this.vhAlgorithm.sortVietnamesebyDESC([...this.list_data], colName);
        }
        break;
      case 'points':
        if (this.pointsCol) {
          this.list_data = this.vhAlgorithm.sortNumberbyASC([...this.list_data], colName);
        } else {
          this.list_data = this.vhAlgorithm.sortNumberbyDESC([...this.list_data], colName);
        }
        break;
      case 'value':
        if (this.valueCol) {
          this.list_data = this.vhAlgorithm.sortNumberbyASC([...this.list_data], colName);
        } else {
          this.list_data = this.vhAlgorithm.sortNumberbyDESC([...this.list_data], colName);
        }
        break;
    }
  }




}
